.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  border: 1px solid rgb(156, 115, 32);
  border-radius: 7px;
}

.itemTitle {
  margin-left: 10px;
  font-weight: bold;
  font-size: 14px;
}
.enter {
  opacity: 0;
  transform: scale(0.1);
}

.enterActive {
  opacity: 1;
  transform: scale(1);
  transition: all 250ms linear;
}
.exit {
  opacity: 1;
  transform: scale(1);
}
.exitActive {
  opacity: 0;
  transform: scale(0.1);
  transition: all 250ms linear;
}

.button {
  display: inline-block;
  color: black;
  font-size: 125%;
  font-weight: 700;
  text-decoration: none;
  user-select: none;
  padding: 0.25em 0.5em;
  outline: none;
  cursor: pointer;
  border: 1px solid rgb(250, 172, 17);
  border-radius: 7px;
  background: rgb(255, 212, 3)
    linear-gradient(rgb(255, 212, 3), rgb(248, 157, 23));
  box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0),
    inset 0 1px 2px rgba(0, 0, 0, 0), inset 0 0 0 60px rgba(255, 255, 0, 0);
  transition: box-shadow 0.2s, border-color 0.2s;
}
.button:hover {
  box-shadow: inset 0 -1px 1px rgba(0, 0, 0, 0),
    inset 0 1px 2px rgba(0, 0, 0, 0), inset 0 0 0 60px rgba(255, 255, 0, 0.5);
}
.button:active {
  padding: calc(0.25em + 1px) 0.5em calc(0.25em - 1px);
  border-color: rgba(177, 159, 0, 1);
  box-shadow: inset 0 -1px 1px rgba(0, 0, 0, 0.1),
    inset 0 1px 2px rgba(0, 0, 0, 0.3), inset 0 0 0 60px rgba(255, 255, 0, 0.45);
}
